.team{
   position: relative;
   overflow: hidden;
   // background: url(http://gidmed.com/wp-content/uploads/2016/03/bobat-terapija.png) center no-repeat;
   background-size: cover;
   &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 65%;
      height: 100%;
      background: var(--grad-bg);
      transform: skew(30deg) translateX(-30%);
      z-index: 1;
   }
   &:after{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to right, transparent,rgba(255, 255, 255, 0.836)), url(https://strollers-club.ru/wp-content/uploads/2019/07/baby_pat2-1cm-o.jpg) center no-repeat;
      background-size: cover;
      opacity: 0.2;
      z-index: 1;
   }
   &__container{
      position: relative;
      z-index: 10;
   }
   &__nav{
      margin-top: 15px;
      padding: 0;
      display: inline-flex;
      border-bottom: 1px solid rgba(255, 255, 255, 0.3);
      & li{
         position: relative;
         display: inline-block;
         padding: 10px;
         color: #fff;
         font-size: 15px;
         font-weight: 700;
         text-transform: uppercase;
         opacity: 0.7;
         cursor: pointer;
         &:not(:last-child){
            margin-right: 10px;
         }
         &.active{
            opacity: 1;
         }
         &.active::before{
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 2px;
            background: #fff;
            transform: translateY(2px);
            transition: all 0.3s;
         }
      }
   }
   &__info-box{
      position: relative;
      margin: 20px 0;
      min-height: 500px;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      perspective: 500px;
   }
   &__item{
      width: 100%;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
      & .img-box{
         position: relative;
         padding: 15px;
         background: #fff;
         width: 40%;
         height: 400px;
         overflow: hidden;
         & img{
            width: 100%;
            height: 100%;
         }
      }
      & .team__desc{
         width: 60%;
         padding: 10px 15px;
         color: var(--color-dark);
         text-align: center;
         & h4{
            display: block;
            margin-bottom: 5px;
            text-align: center;
            font-size: 22px;
            color: var(--color-dark);
            text-transform: uppercase;
         }
         & span.pos{
            display: inline-block;
            margin-bottom: 20px;
            color: var(--color-blue);
         }
         & p.text{
            line-height: 1.3;
            margin-bottom: 20px;
            color: rgba(0,0,0,0.7);
         }
         & a.btn{
            color: #fff;
         }
      }
   }
   &__slider{
      position: relative;
      margin-top: 25px;
      width: 100%;
      height: 400px;
      overflow: hidden;
      &-slide{
         position: relative;
         width: 250px;
         height: 100%;
         display: flex;
         flex-direction: column;
         justify-content: flex-start;
         align-items: center;
         background: #fff;
         border: 1px solid transparent;
         transition: border 0.2s, box-shadow 0.2s linear, transform 0.2s ease-in;
         &:not(:last-child):before{
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            width: 1px;
            height: 100%;
            background: linear-gradient(to bottom,transparent,var(--color-blue),transparent);
         }
         & img{
            display: block;
            width: 100%;
            height: 300px;
            margin-bottom: 10px;
          
         }
         & span.name{
            display: block;
            padding: 0 10px;
            width: 100%;
            color: var(--color-blue);
            text-align: center;
            font-size: 18px;
            text-transform: uppercase;
            font-weight: 700;
            margin-bottom: 10px;
            transition: color 0.4s;
         }
         & span.pos{
            display: block;
            padding: 0 10px;
            font-size: 15px;
            text-align: center;
            width: 100%;
            color: var(--color-dark);
            transition: color 0.4s;
         }
         &:hover{
            transform: scale(1.1);
         }
      }
   }
}
.pacient__slider{
   position: relative;
   margin-top: 25px;
   width: 100%;
   height: 400px;
   overflow: hidden;
   &-slide{
      position: relative;
      width: 250px;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      background: #fff;
      border: 1px solid transparent;
      transition: border 0.2s, box-shadow 0.2s linear, transform 0.2s ease-in;
      & img{
         display: block;
         width: 100%;
         height: 300px;
         margin-bottom: 10px;
       
      }
      & span.name{
         display: block;
         padding: 0 10px;
         width: 100%;
         color: var(--color-blue);
         text-align: center;
         font-size: 18px;
         text-transform: uppercase;
         font-weight: 700;
         margin-bottom: 10px;
         transition: color 0.4s;
      }
      & span.pos{
         display: block;
         padding: 0 10px;
         font-size: 15px;
         text-align: center;
         width: 100%;
         color: var(--color-dark);
         transition: color 0.4s;
      }
      &:hover{
         box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
        border: 1px solid var(--color-blue);
      }
   }
}
.team-prev,
.team-next,
.pacient-next,
.pacient-prev{
   background: var(--color-dark);
   width: 50px;
   height: 50px;
   opacity: 1;
   z-index: 10;
   visibility: visible;
   transition: opacity 0.3s;
   &:hover{
      opacity: 1;
   }
   &:after{
      color: #fff;
   }
}
.team-prev{
   bottom: 0;
   left: -50px;
}
.team-next{
   bottom: 0;
   right: -50px;
}

.team__item{
   position: absolute;
   top: 50%;
   right: 0;
   
   opacity: 0;
   transform: translateX(50px) translateY(-50%);
   visibility: hidden;
   pointer-events: none;
 
   transition: opacity 0.5s, transform 0.5s ease-in;
   &.show{
     
      opacity: 1;
      transform: translateX(0) translateY(-50%);
      visibility: visible;
      pointer-events: all;
      z-index: 20;
   }
}