.footer{
   background: var(--grad-bg);
   &__container{
      display: flex;
      justify-content: space-between;
      align-items: center;
   }
   &__logo{
      width: 40%;
      display: flex;
      align-items: center;
      & img{
         display: block;
         width: 100px;
         height: 100px;
         margin-right: 10px;
      }
      & h4{
         display: block;
         color: #fff;
         font-size: 18px;
         & span{
            font-size: 15px;
         }
      }
   }
   &__contacts{
      width: 50%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      color: #fff;
      & i{
         display: inline-block;
         margin-right: 7px;
         transition: transform 0.3s ease-in-out;
         &:hover{
            transform: scale(1.1);
         }
      }
      & span,
      & a{
         &:hover{
            text-decoration: underline;
         }
      }
   }
}