.partners{
   &__container{
      background: #f5f5f5;
      
   }
   &__inner{
      width: 80%;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      & a{
         position: relative;
         display: block;
         width: 190px;
         height: 100px;
         margin: 10px;
         transition: transform 0.4s ease-in;
         & img{
            width: 100%;
            height: 100%;
         }
         &:hover{
            transform: scale(1.1);
         }
      }
   }
}