.news{
   position: relative;
   background: #f2f2f2;
   &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: linear-gradient(to right,transparent,var(--color-blue),transparent);
      opacity: 0.3;
   }
  
   &__container{
      position: relative;
      display: flex;
      justify-content: space-between;
      margin: 30px 0;
      z-index: 5;
   }
   &__inner{
      width: 70%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      transform: translateY(50px);
  opacity: 0;
  transition: transform 0.4s ease-in, opacity 0.4s;
  &._active{
   transform: translateY(0);
   opacity: 1;
  }
   }
   &__item{
      position: relative;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;     
      width: 100%;
      padding: 2rem;
      background: #fff;
      
      box-shadow: .5rem .5rem 3rem 1px rgba(0,0,0,0.05);
      &:not(:last-child){
         margin-bottom: 15px;
      }
      &-img{
         position: relative;
         display: block;
         min-width: 25rem;
         max-width: 25rem;
         height: 20rem;
         transform: translateX(-4rem) rotate(-30deg);
         opacity: 0;
         overflow: hidden;
         transition: transform 0.4s ease-in, opacity 0.7s ease-in;
         &._active{
            transform: translateX(-4rem) rotate(0);
            opacity: 1;
         }
         &:before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: var(--color-dark);
            box-shadow: .5rem .5rem 3rem 1px rgba(0,0,0,0.05);
            opacity: 0.2;
         }
         & img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: transform 0.6s ease-in;
         }
      }
      & span.data{
         display: block;
         text-align: left;
         padding: 5px;
         font-size: 13px;
         font-weight: 700;
         margin-bottom: 10px;
         opacity: 0.4;
         & i{
            display: inline-block;
            margin-right: 7px;
            opacity: 0.5;
         }
      }
      & h3.news__item-title{
         display: block;
         margin-bottom: 15px;
         text-transform: uppercase;
         color: var(--color-dark);
         font-size: 22px;
      }
      &-text{
         margin-bottom: 3rem;
         font-size: 18px;
         line-height: 1.3;
         color: rgba(0,0,0,0.7);
      }
      &-btn{
         display: inline-block;
         background: var(--grad-bg);
         padding: 10px 15px;
         letter-spacing: 1px;
         color: #fff;

      }
      &:hover{
         & .news__item-img img{
            transform: scale(1.1);
         }
      }
   }
   &__about{
      position: relative;
      width: 25%;
      height: inherit;
      text-align: center;
      padding: 10px;
      color: #fff;
      background: var(--grad-bg);
      box-shadow: var(--shadow);
      transform: translateX(30px);
      opacity: 0;
      perspective: 1600px;
      transition: transform 0.4s ease-in, opacity 0.4s;
      & .about-img{
         display: inline-block;
         width: 80%;
         margin: 0 auto;
         transform: translateY(-40px) translateZ(20px);
         box-shadow: .5rem .5rem 3rem 1px rgba(0,0,0,0.05);

      }
         &._active{
            transform: translateX(0);
            opacity: 1;
         }
      & h5{
         display: block;
         padding: 10px 0;
         font-size: 22px;
         text-align: center;
      }
      & p{
         margin-bottom: 15px;
         padding: 10px;
      }
      & a.btn{
         color: var(--color-blue);
         background: #fff;
      }
   }
}

.news__info{
   position: relative;
   display: flex;
   justify-content: space-between;
   align-items: center;
   z-index: 10;
}
.news__contact{
   position: relative;
   width: 40%;
   min-height: 200px;
   
   background: linear-gradient(to right,#0093c89d,#0093c8), url(https://copypast.ru/foto7/2327/deti_karandashom_2.jpg);
   background-size: cover;
   background-position: 0 60%;
   &-content{
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 100%;
      padding: 10px;
      text-align: center;
      & h4{
         display: block;
         margin-bottom: 20px;
         color: #fff;
      }
      & a{
         display: inline-block;
         padding: 5px 10px;
         color: #fff;
         text-transform: uppercase;
         border: 1px solid #fff;
         transition: transform 0.3s ease-in;
         &:hover{
            transform: scale(1.1);
         }
      }
   }
}
.news__anons{
   width: 55%;
   min-height: 200px;
   background: #fff;
  & .block-title{
     display: block;
     color: #fff;
     background: var(--color-blue);
     font-size: 22px;
     font-weight: 700;
     border-bottom: 1px solid #03c6e04d;
     & i{
      display: inline-block;
      padding: 5px;
      background: var(--color-dark);
      color: #fff;
      margin-right: 7px;
    }
  }
  &-list{
     width: 100%;
     padding: 15px 10px;
     & li{
        display: block;
        padding: 5px 0;
        & span.data{
           display: inline-block;
           margin-right: 7px;
           font-size: 12px;
           opacity: 0.4;
           border-bottom: 1px dotted rgba(0, 0, 0, 0.2);
        }
        & a{
           font-size: 15px;
           color: var(--color-blue);
           &:hover{
            text-decoration: underline;
           }
        }
     }
  }
}