   &__container{
      position: relative;
      height: 100%;
      padding: 0 0 0px 0;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url(http://pgia2.edu.ru/wp-content/uploads/2020/04/s1200.jpg) center no-repeat;
        background-size: contain;
      }
     
   }
   &__sidebar{
      width: 100%;
      min-width: 250px;
      padding: 15px 0 0 0;
      
      transform: translateY(-50%);
      z-index: 10;
      & .nav-list{
         width: 100%;
         height: auto;
         display: flex;
         
         & li{
            position: relative;
            display: block;
            width: 15%;
            padding: 15px 0;
            text-align: center;
            background: var(--grad-bg);
            perspective: 1600px;
            transition: background 0.3s ease-in, box-shadow 0.3s ease;
            &:not(:last-child):before{
               content: '';
               position: absolute;
               top: 0;
               right: 0;
               width: 1px;
               height: 100%;
               background: linear-gradient(to bottom, transparent,#f8fafa,transparent);
            }
            & svg{
              width: 50px;
              height: 50px;
              fill: #fff;
              transition: transform 0.2s ease-in;
            }
            & a{
               display: block;
               width: 100%;
               padding: 10px;
               color: #fff;
               background: transparent;
               text-align: center;
               transform:translateZ(100px);
               transition: background 0.2s ease-in;
               &:hover{
                  text-decoration: underline;
               }
            }
            &:hover{
              box-shadow: 1px 2px 2px rgba(0,0,0,0.5);
              background: linear-gradient(175deg,var(--color-blue),#02a5bb);
              & svg{
                transform: rotate(-5deg);
              }
            }
         }
      }
   }
  
   &__content{
      position: absolute;
      top: 0;
      left: 0;
      width: 40%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background: linear-gradient(to right,#009fb3 70%,transparent);
    
      & img{
         display: block;
         width: 70px;
         height: 70px;
         border-radius: 50%;
      }
      & h1{
         display: block;
         text-align: center;
      }
   }
