}
%transition_all_03s {
   transition:all .3s ease;
 }
 %backface_visibility_hidden{
   backface-visibility:hidden;
   -webkit-backface-visibility:hidden;
 }
.hero__slider {
   width: 100%;
   height: 450px;
   overflow: hidden;
   transition:opacity .6s ease, transform .3s ease;
   &:hover{
     .swiper-button-prev,
     .swiper-button-next{
       transform:translateX(0);
       opacity:1;
       visibility:visible;
     }
   }
   &.loading{
     opacity:0;
   }
   &.scale-out{
     transform:scale(.7);
   }
   &.scale-in{
     transform:scale(1);
   }
 }
 .swiper-wrapper{
 
 }
 .swiper-slide{
   background-position:center;
   background-size: cover;
   background-position: 0% 50%;
   perspective:1600px;
   
   .entity-img{
     display:none;
   }
   .content{
     position:absolute;
     top:40%;
     left:0;
     width:50%;
     padding-left:5%;
     color:#fff;
     transform:translateZ(100px) translateX(100px);
     .title{
       display: inline-block;
       font-size:1.5em;
       padding: 10px;
       font-weight:bold;
       color: var(--color-dark);
       background: rgba(250, 250, 250, 1);
       transform: translateX(20%);
       opacity: 0;
         transition: opacity 0.7s, transform 0.7s ease-in-out;
     }
     .caption{
       display:block;
       padding: 10px;
       font-size:18px;
       line-height:1.4;
       color: var(--color-dark);
       background: rgba(250, 250, 250, 1);
       transform: translateX(-20%);
       opacity: 0;
         transition: opacity 0.7s, transform 0.7s ease-in-out;
     }
   }
   &.swiper-slide-active{
      z-index:2;
      .title{
         transform: translateX(0);
       opacity: 1;
       transition-delay: 0.5s;
      }
      .caption{
         transform: translateX(0);
       opacity: 1;
       transition-delay: 0.5s;
      }
    }
 }
 [class^="swiper-slide-shadow-"]{
   @extend %backface_visibility_hidden;
 }
 [class^="swiper-button-"]{
   width:44px;
   opacity:0;
   visibility:hidden;
   @extend %transition_all_03s;
 }
 .swiper-button-prev{
   transform:translateX(50px);
 }
 .swiper-button-next{
   transform:translateX(-50px);
 }
 .swiper-container-horizontal{
   >.swiper-pagination-bullets{
     .swiper-pagination-bullet{
       margin:0 9px;
       position:relative;
       width:12px;
       height:12px;
       background-color:#fff;
       opacity:.4;
       @extend %transition_all_03s;
       &::before{
         content:'';
         position:absolute;
         top:50%;
         left:50%;
         width:18px;
         height:18px;
         transform:translate(-50%, -50%);
         border:0px solid #fff;
         border-radius:50%;
         @extend %transition_all_03s;
       }
       &:hover,
       &.swiper-pagination-bullet-active{
         opacity:1;
       }
       &.swiper-pagination-bullet-active{
         &::before{
           border-width:1px;
         }
       }
     }
   }
 }

