.header{
   position: relative;
   width: 100%;
   z-index: 100;
   background: var(--grad-bg);
   &__top{
      position: relative;
      height: 50px;
      background: #fff;
      padding: 0 50px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      color: var(--color-dark);
      &:before{
         content: '';
         position: absolute;
         top: 0;
         right: 0;
         width: 40%;
         height: 100%;
         background: #f5f5f5;
         transform: skewX(-20deg) translateX(20%);
      }
      & .contacts{
         flex-grow: 1;
         display: flex;
         justify-content: center;
         align-items: center;
      }
      & i{
         font-family: fontawesome !important;
         display: inline-block;
         margin-right: 7px;
         font-size: 18px;
         transform: translateY(0);
         color: var(--color-blue);
      }
      & span.adress{
         display: inline-block;
         margin-right: 30px;
      }
      & a{
         display: inline-block;
         margin-right: 30px;
         transition: all 0.2s;
         &:hover{
            text-decoration: underline;
         }
         &.version{
            margin-left: auto;
            display: inline-block;
            padding: 0 10px;
            height: 100%;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            transition: all 0.2s ease-in;
            z-index: 5;
            & i{
               transform: translateY(0);
            }
            &:hover{
               text-decoration: underline;
            }
         }
      }
      & .header__lang{
         height: 100%;
         display: inline-flex;
         align-items: center;
         & a{
            position: relative;
            display: inline-block;
            height: 100%;
            padding: 0 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 0;
            color: var(--color-blue);
            font-weight: 700;
            &:first-child::before{
               content: '';
               position: absolute;
               top: 0;
               right: 0;
               width: 1px;
               height: 100%;
               background: linear-gradient(to bottom,transparent,var(--color-blue),transparent);
            }
            &:hover{
               color: #fff;
               text-decoration: none;
               background: #017a8a;
            }
         }
      }
   }
   &__container{
      display: flex;
      justify-content: space-between;
      align-items: center;
   }
   &__logo{
     
      display: flex;
      align-items: center;
      height: 100%;
      & img{
         width: 100px;
         height: 100%;
      }
      & h1{
         display: block;
         margin-left: 10px;
         line-height: 100%;
         font-size: 26px;
         color: #fff;
         & span{
            display: block;
            font-size: 18px;
         }
      }
   }
}

.burger{
   display: none;
}