.services{
   background: #fff;
   &__inner{
      padding-top: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
   }
   &__item{
      position: relative;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    width: 30%;
    min-height: 150px;
    margin: 15px;
    padding: 15px;
    background: var(--color-blue);
    display: flex;
    justify-content: center;
    align-items: center;
    transform: scale(0);
  opacity: 0;
  transition: transform 0.5s ease-in, opacity 0.5s;
  &._active{
   transform: scale(1);
   opacity: 1;
  }
    & span.number{
       position: absolute;
       top: -20px;
       left: -10px;
       display: inline-block;
       font-size: 46px;
       font-weight: 700;
       color: #222;
    }
    & span.title{
       display: block;
       text-align: center;
       font-weight: 700;
       color: #fff;
    }
   }
}